<template>
  <div class="ape-bg w-full overflow-hidden pb-[200px]">
    <Nav />
    <div class="md:max-w-[1200px] mx-auto md:px-[124px]">
      <div class="flex flex-col md:flex-row md:justify-between items-center md:items-end mt-[130px]">
        <img
          class="md:w-[108px] md:h-[50px] w-[23.2vw] h-[10.667vw] mb-[11px]"
          :src="$i18n.locale === 'zh' ? require('../../assets/pledge/title.png') : require('../../assets/pledge/title_en.png')"
          alt="title"
        />
        <div class="flex w-full md:w-auto items-center justify-end md:justify-center md:pr-[30px] pr-[6.133vw] mt-[10.267vw] md:mt-0">
          <button class="light-btn md:w-[188px] md:h-[72px] w-[28.067vw] h-[12.6vw] md:text-[14px] text-[2.667vw]" @click="startClaim()">
              <!-- 一键领取 -->
            {{ $t('claimAll') }} <Loading v-if="claimLoading" />
          </button>
          <button
            class="dark-btn md:w-[185px] md:h-[68px] w-[28.667vw] h-[12.067vw] ml-[4vw] md:ml-[30px] text-white md:text-[14px] text-[2.667vw]"
            @click="startWithdraw()"
          >
          <!-- 一键解压 -->
            {{ $t('withdrawAll') }} <Loading v-if="withdrawLoading" />
          </button>
        </div>
      </div>
      <div class="md:mt-[70px] mt-[14.267vw] md:p-[12px] px-[2.667vw]">
        <el-row :gutter="40">
          <TransitionGroup name="fade">
            <el-col :xs="12" :sm="12" :md="8" v-for="nft in NFTlist" :key="nft.tokenId">
              <div
                class="
                  w-full
                  flex flex-col
                  items-center
                  md:mb-[100px]
                  mb-[16vw]
                  transfrom-zoom-scale
                "
              >
                <div class="relative md:w-[210px] md:h-[210px] w-[36.533vw] h-[36.533vw]">
                  <a target="_blank" :href="nft.requestImgUrl">
                    <img
                      class="w-full h-full"
                      :src="nft.requestImgUrl"
                      alt=""
                    />
                    <img
                      class="
                        absolute
                        top-0
                        bottom-0
                        z-10
                        w-full
                        h-full
                        scale-110
                      "
                      src="../../assets/pledge/border.png"
                      alt="border"
                    />
                  </a>
                </div>
                <p class="md:mt-[20px] mt-[5.067vw] text-[2.933vw] md:text-[12px] text-white mb-[6.533vw] md:mb-[44px]">
                  {{ nft.name }}: {{ nft.tokenId.toString() }} /
                  {{ nft.nftBalance.toString() }}
                </p>
                <div
                  v-if="nft.depositeds.id"
                  class="flex md:flex-row flex-col items-center md:text-[14px] text-[2.933vw]"
                >
                  <button
                    class="dark-btn md:w-[145px] md:h-[62px] w-[29.333vw] h-[10.667vw] text-white"
                    @click="startClaim(nft.depositeds.id)"
                  >
                    <!-- 领取收益 -->
                    {{ $t('claim') }}<Loading v-if="nft.depositeds.id === claimItemLoading" />
                  </button>
                  <button
                    class="light-btn md:w-[157px] md:h-[72px] w-[29.867vw] h-[11.333vw]"
                    @click="startWithdraw(nft.depositeds.id)"
                  >
                    <!-- 解压 -->
                    {{ $t('withdraw') }}<Loading v-if="nft.depositeds.id === withdrawItemLoading" />
                  </button>
                </div>
                <button
                  v-else
                  class="light-btn md:w-[188px] md:h-[72px] w-[29.867vw] h-[11.333vw]"
                  @click="startPledge(nft.tokenId)"
                >
                  Pledge<Loading v-if="nft.tokenId === pledgeItemLoading" />
                </button>
              </div>
            </el-col>
          </TransitionGroup>
        </el-row>
        <div class="md:w-[50%] w-[80%] mx-auto" v-if="loadNFT !== 101 && loadNFT !== -1">
          <p class="text-white text-center mb-[20px]">
           {{  $t('loadNFT') }}
          </p>
          <el-progress :percentage="loadNFT" color="#00ffff"></el-progress>
        </div>

        <div class="mt-[80px]">
          <div
            class="
              rule-btn
              w-[184px]
              h-[76px]
              leading-[76px]
              text-[14px] text-white text-center
            "
          >
            <!-- 质押产出规则: -->
            {{ $t('staking') }}
          </div>
          <p class="text-[12px] pl-[20px] opacity-75 text-white mt-[10px]">
            <!-- 总挖矿日期90天停止， -->
            {{ $t('rule') }}
          </p>
          <p class="text-[12px] pl-[20px] opacity-75 text-white md:leading-[28px] leading-[5.067vw]">
            <!-- A系列nft质押挖矿，每个挖出3万枚币，90天挖完，每天平均333.33枚;
            B系列nft每个挖矿1.8万枚90天，每天200枚;
            C系列，每个挖矿1.3万枚，90天，每天144.4枚;
            D系每个挖矿1w枚，90天每天，111.1枚。 -->
            {{ $t('ruleTxt') }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from "vue";
import { useStore } from "vuex";
import Nav from "../../components/Nav";
import Loading from "../../components/Loading";
import { ElMessage } from 'element-plus';
export default {
  components: {
    Nav,
    Loading,
  },
  setup() {
    const store = useStore();
    const withdrawLoading = ref(false);
    const claimLoading = ref(false);
    const claimItemLoading = ref(-1);
    const withdrawItemLoading = ref(-1);
    const pledgeItemLoading = ref(-1);
    const startPledge = async (tokenId) => {
        ElMessage.info("Coming Soon")
        return;
      pledgeItemLoading.value = tokenId
      await store.dispatch("deposit/startDeposit", tokenId);
      pledgeItemLoading.value = -1
    };
    const startWithdraw = async (id) => {
        ElMessage.info("Coming Soon")
        return;
      if (withdrawLoading.value|| claimItemLoading.value !== -1) return;
      id ? withdrawItemLoading.value = id : withdrawLoading.value = true;
      await store.dispatch("deposit/withdraw", id);
      id ? withdrawItemLoading.value = -1 : withdrawLoading.value = false;
    };
    const startClaim = async (id) => {
        ElMessage.info("Coming Soon")
        return;
    console.log(id);
      if (claimLoading.value || claimItemLoading.value !== -1) return;
      id ? claimItemLoading.value = id : claimLoading.value = true;
      await store.dispatch("deposit/claim", id);
      id ? claimItemLoading.value = -1 : claimLoading.value = false;
    };

    console.log(store, "storestorestore");

    return {
      loadNFT: computed(() => store.getters.deposit.loadNFT),
      NFTlist: computed(() => store.getters.deposit.NFTlist),
      startPledge,
      startWithdraw,
      startClaim,
      withdrawLoading,
      claimLoading,
      pledgeItemLoading,
      claimItemLoading,
      withdrawItemLoading
    };
  },
};
</script>

